<template>
  <div id="header">
    <span class="logo-title">中大科技实验室系统</span>
    <div class="menu-options">
      <div :class="this.headerType == 'home' ? 'menu-item active' : 'menu-item'" @click="gotoRouter('home')">
        首页
      </div>
      <div :class="this.headerType == 'detection' ? 'menu-item active' : 'menu-item'" @click="gotoRouter('detection')">
        检验检测
      </div>
      <div :class="this.headerType == 'instrument' ? 'menu-item active' : 'menu-item'"
        @click="gotoRouter('instrument')">
        仪器设备
      </div>
      <!-- <div :class="this.headerType == 'quality' ? 'menu-item active' : 'menu-item'"
        @click="gotoRouter('quality')">
        质量管控
      </div> -->
    </div>
    <div class="tools">
      <div class="weather">
        <img src="../../assets/images/icon_duoyun.png" class="weather-img">
        <div class="weather-detail">
          <span>{{ temperature }}℃</span>
          <span>{{ weather }}</span>
        </div>
      </div>
      <div class="date">
        <div class="weekend">
          <span>{{ dateWeek }}</span>
          <span>{{ dateYear }}</span>
        </div>
        <div class="time">
          {{ dateDay }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import { formatTime } from '@/utils/index.js'
export default {
  data () {
    return {
      timing: null,
      dateDay: null,
      dateYear: null,
      dateWeek: null,
      weekday: ['星期日', '星期一', '星期二', '星期三', '星期四', '星期五', '星期六'],
      weather: "",
      temperature: ""
    }
  },
  mounted () {
    this.timeFn()
    this.getWeather()
  },
  beforeDestroy () {
    clearInterval(this.timing)
  },
  components: {
  },
  props: {
    headerType: {
      type: String,
      default: "home"
    }
  },
  watch: {
  },
  methods: {
    timeFn () {
      this.timing = setInterval(() => {
        this.dateDay = formatTime(new Date(), 'HH:mm:ss')
        this.dateYear = formatTime(new Date(), 'yyyy.MM.dd')
        this.dateWeek = this.weekday[new Date().getDay()]
      }, 1000)
    },
    gotoRouter (name) {
      if (name !== this.headerType)
        this.$router.push({ name: name })
    },
    getWeather () {
      axios.get("https://restapi.amap.com/v3/weather/weatherInfo?key=e3aed85dd6fe8b2017da03c56e18b3b9&city=430100").then(res => {
        console.log(res.data.lives)
        this.weather = res.data.lives[0].weather
        this.temperature = res.data.lives[0].temperature
      })
    }
  },
}
</script>

<style lang="scss" scoped>
