  width: 100%;
  height: 91px;
  background-image: url("~@/assets/images/Top.png");
  position: relative;

  .logo-title {
    width: 443px;
    height: 65px;
    font-size: 45px;
    font-family: YouSheBiaoTiHei;
    font-style: oblique;
    font-weight: 550;
    color: #FFFFFF;
    line-height: 65px;
    letter-spacing: 3px;
    background: linear-gradient(180deg, #FFFFFF 0%, #8AEEFF 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    position: absolute;
    left: 41px;
    top: 7px;
  }

  .menu-options {
    display: flex;
    position: absolute;
    left: 565px;
    top: 39px;

    .menu-item {
      width: 183px;
      height: 46px;
      background-image: url("~@/assets/images/button.png");
      margin-right: 9px;
      color: #D0EFFF;
      display: flex;
      align-items: center;
      justify-content: center;

      &:hover {
        cursor: pointer;
        background-image: url("~@/assets/images/button_1.png");
        color: #FFFFFF;
      }
    }

    .menu-item.active {
      background-image: url("~@/assets/images/button_1.png");
      color: #FFFFFF;
    }
  }

  .tools {
    display: flex;
    position: absolute;
    left: 1580px;
    top: 34px;
    // width: 200px;
    height: 30px;
    // background-color: #fff;

    .weather {
      color: rgba(255, 255, 255, 0.8);
      display: flex;
      // padding-right: 18px;
      border-right: 1px solid rgba(255, 255, 255, 0.6);

      .weather-img {
        width: 26px;
        height: 26px;
        margin-right: 4px;
      }

      .weather-detail {
        display: flex;
        width: 50px;
        flex-direction: column;
        justify-content: center;
        color: rgba(255, 255, 255, 0.8);
      }
    }

    .date {

      display: flex;

      // position: absolute;
      // right: 24px;
      .weekend {
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding-left: 15px;
        color: rgba(255, 255, 255, 0.8);
      }

      .time {
        // width: 133px;
        // height: 45px;
        // line-height: 45px;
        display: flex;
        align-items: center;
        margin-left: 15px;
        color: #96CEFF;
        font-size: 32px;
      }
    }
  }
}
</style>