import request from '@/utils/request'

let isMock = request.defaults.baseURL.indexOf("apifoxmock") > -1;

/**
 * 机构数量，省市
 * @returns
 */
export function getCitiesNumApi() {
  if (isMock) {
    return request({
      url: "/largeScreenData/getCitiesNumApi",
      method: "get",
    });
  }
  return new Promise((resolve, reject) => {
    resolve({
      code: 0,
      msg: "操作成功",
      data: {
        num1: 24,
        num2: 18,
      },
      total: 0,
    });
  });
}

